import React from 'react';
import { Switch, Route} from 'react-router-dom';
import Home from './Pages';
import {GenrePage, Page1, MoviePage, SearchPage, TopRated, UserList} from './Pages';
// import {GENRE, ErrorImageSrc, BackgroundImg} from './Constants';

class Main extends React.Component {
	render() {
		return(
			<main style={{marginTop:50}} >
				<Switch>
			      <Route exact path='/Movie-Website/' component={Home} />
			      <Route path='/Movie-Website/Recommend' component={Page1} />
			      <Route path='/Movie-Website/movie-page/:title' render={(props) => <MoviePage 
			      	email={this.props.email} 
			      	isAuthenticated={this.props.isAuthenticated}
			      	{...props} />} />
			      <Route path='/Movie-Website/Top' component={TopRated} />
			      <Route path='/Movie-Website/Popular' component={Page1} />
			      <Route path='/Movie-Website/AdvancedSearch' component={SearchPage} />
			      <Route path='/Movie-Website/genre/:type' render={(props) => <GenrePage pageLimit={24} {...props} />} />
			      <Route path='/Movie-Website/user/:type' render={(props) => <UserList 
			      	email={this.props.email} 
			      	isAuthenticated={this.props.isAuthenticated}
			      	pageLimit={24} {...props} />} />
			    </Switch>
			</main>
		);
	}
}


export default Main

